import React, { useState, useEffect } from "react";
import { Element } from "react-scroll";
// import Popup from "reactjs-popup";
import './index.css'

import { Box } from "../components/Core";
import PopupBox from "../components/Popup";
import PageWrapper from "../components/PageWrapper";
import Hero from "../sections/landing5/Hero";
import Works from "../sections/landing5/Works";
import CTA from "../sections/landing5/CTA";

const IndexPage = () => {
  const [showPopup, setShowPopup] = useState(true);

  const openPopup = () => {
    setShowPopup(true);
  };

  const handleClose = () => {
    setShowPopup(false); // Update isOpen to false to hide the popup
  };

  // useEffect(() => {
  //   // Check a condition to determine whether the popup should be shown.
  //   // For example, you can check if a user has visited the site before.
  //   openPopup();
  //   console.log("show popup? inside useEffect", showPopup);
  //   // const hasVisitedBefore = localStorage.getItem('visitedBefore');

  //   // if (!hasVisitedBefore) {
  //   //   // If the user hasn't visited before, set showPopup to true and store that they have visited.
  //   //   setShowPopup(true);
  //   //   localStorage.setItem('visitedBefore', 'false');
  //   // }
  // }, []);

  // console.log("show popup?", showPopup);

  return (
    <>
      <PageWrapper darkTheme>
        <Box className="box" bg="info">
          {showPopup && <PopupBox className="popup" isOpen = {showPopup}
            handleClose={handleClose}/>}
          <Hero className="hero"/>
          {/* <Popup  */}
            {/* // trigger={showPopup} */}
            {/* open={showPopup}
            position="right center"> */}
            {/* {showPopup && ( */}
            {/* ) } */}
            {/* <div><h1>hello</h1></div> */}
          {/* </Popup> */}
          <Element name="works">
            <Works />
          </Element>
          <CTA />
        </Box>
      </PageWrapper>
    </>
  );
};
export default IndexPage;
